<template>
  <div class="row login-top">
    <div class="col-md-4">
    </div>
    <div class="col-md-4">
      <va-box
        title="农贷通运维子系统"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">


          <form class="form-horizontal" id="box">
            <div class="form-group">
              <label for="inputEmail3" class="col-sm-2 control-label">姓名</label>
              <div class="col-sm-10">
                <input type="text" class="form-control" id="inputEmail3"  v-model="loginInfo.username" placeholder="请输入您的姓名">
              </div>
            </div>
            <div class="form-group">
              <label for="inputPassword3" class="col-sm-2 control-label">密码</label>
              <div class="col-sm-10">
                <input type="password" class="form-control" id="inputPassword3"  v-model="loginInfo.password" placeholder="请输入您的密码">
              </div>
            </div>
          </form>

        </div>

        <div slot="footer">

          <button class="btn btn-primary" type="submit" @click="handleLogin">登   陆</button>
          <va-button :isBlock="false" theme="info" align="right" name="忘记密码"></va-button>

        </div>

      </va-box>
    </div>
    <div class="col-md-4">
    </div>
  </div>
</template>

<script>
import VABox from '../components/VABox'
import VAInput from '../components/VAInput'
import VAButton from '../components/VAButton'
// import axios from 'axios'

export default {
  name: 'login',
  data () {
    return {
      userUrl: 'https://private-anon-a57d5fac41-ndt.apiary-mock.com/api/auth/session',
      loginInfo: {
        username: '',
        password: ''
      }
    }
  },
  created () {
  },
  components: {
    'va-box': VABox,
    'va-input': VAInput,
    'va-button': VAButton
  },
  methods: {
    handleLogin: function () {
      let vm = this
      vm.$router.push('/user')
      // axios.post(vm.userUrl, vm.loginInfo)
      // .then(function (response) {
      //   console.log(response)
      //   localStorage.setItem('token', response.data.token)
      //   localStorage.setItem('name', response.data.user.username)
      //   vm.$router.push('/user')
      // })
      // .catch(function (error) {
      //   console.log(error)
      // })
    }
  }
}

</script>

<style type="text/css">
  .login-top{
    margin-top: 10%;
  }
</style>



