<template>
    <va-box
          title="文件上传"
          theme="box-success"
          :isBorder="true"
          widgetType="">

      <div slot="content">

      <column class="col-xs-4">
      </column>

      <column class="col-xs-4">

      <div style="border: 1px solid #eee; padding: 20px;border-radius: 5px">
        <form action="" enctype="multipart/form-data" method="">

          <va-input-group
            title="上传文件"
            vaId="InputFile"
            type="file"
            helpBlock=""
          >
          </va-input-group>

          <va-form-group>
            <label>来源</label>
            <va-select
              :list="SourceList"
            ></va-select>
          </va-form-group>

          <va-form-group>
            <label>应用类型</label>
            <va-select
              :list="TypeList"
            ></va-select>
          </va-form-group>

          <div slot="footer">

            <va-button :isBlock="false" theme="primary" name="上传">
            </va-button>
            <va-button :isBlock="false" theme="primary" align="right"
              name="取消">
            </va-button>

          </div>

        </form>
  
      </div>
      
      </column>

      </div>

    </va-box>
</template>

<script>
import VABox from '../../components/VABox.vue'
import VAInputGroup from '../../components/VAInputGroup.vue'
import VAInput from '../../components/VAInput.vue'
import VAButton from '../../components/VAButton.vue'
import VASelect from '../../components/VASelect.vue'
import VAFormGroup from '../../components/VAFormGroup.vue'

export default {
  name: 'upload',
  data () {
    return {
      SourceList: [
        'xxx银行1',
        'xxx银行2',
        'xxx银行3',
        'xxx银行4',
        'xxx银行5'
      ],
      TypeList: [
        '核心账务1',
        '核心账务2',
        '核心账务3',
        '核心账务4',
        '核心账务5'
      ]
    }
  },
  created () {

  },
  components: {
    'va-box': VABox,
    'va-input-group': VAInputGroup,
    'va-input': VAInput,
    'va-button': VAButton,
    'va-form-group': VAFormGroup,
    'va-select': VASelect
  }
}
</script>



