<template>
  <div class="page">
    <div class="box box-success" v-if="isBox">

      <div class="box-header">

        <slot name="icon-title"></slot>
        <h3 class="box-title">
           <button class="btn btn-primary" type="button">添加</button>
        </h3>

        <div class="box-tools col-md-4">
          <div class="input-group">
            <input type="search" class="form-control" placeholder="Search for...">
            <span class="input-group-btn">
              <button class="btn btn-primary" type="button">搜索</button>
            </span>
          </div>
        </div>
      </div>

      <div class="box-body">
        <slot name="content"></slot>
      </div>

      <div v-if="$slots.footer" class="box-footer">
        <nav class="boot-nav">
          <ul class="pagination boot-page">
            <li>
              <a href="javascript:void(0)"  @click="wholePrevClick()">
                <span aria-hidden="true">首页</span>
              </a>
            </li>
            <li>
              <a href="javascript:void(0)" aria-label="Previous" @click="onPrevClick()">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li v-for="(page, index) in pages" :class="activeNum === index ? 'active' : ''">
              <a href="javascript:void(0)" v-text="page" @click="onPageClick(index)"></a>
            </li>
            <li>
              <a href="javascript:void(0)" aria-label="Next" @click="onNextClick()">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
            <li>
              <a href="javascript:void(0)"  @click="wholeNextClick()">
                <span aria-hidden="true">尾页</span>
              </a>
            </li>
          </ul>
          <div class="page-total">
            共 <span v-text="pageTotal"></span> 页
          </div>
        </nav>
      </div>

      <div v-if="this.isLoading" class="overlay">
        <i class="fa fa-refresh fa-spin"></i>
      </div>
    </div>
    <div v-else>
      <slot name="content"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    // 每页显示个数
    len: {
      type: Number,
      default: 2
    },
    // 表格数据（数组）
    data: {
      type: Array,
      default () {
        return []
      }
    },
    // AJAX地址
    url: {
      type: String,
      default: ''
    },
    // 当前页的字段
    currentPage: {
      type: String,
      default: ''
    },
    totalPages: {
      type: String,
      default: ''
    },
    totalName: {
      type: String,
      default: ''
    },
    // 显示页数
    pageLen: {
      type: Number,
      default: 10
    },

    // 参数内容
    param: {
      type: Object,
      default () {
        return {}
      }
    },
    // method
    method: {
      type: String,
      default: 'get'
    },
    isLoading: { type: Boolean, default: false },
    isBox: { type: Boolean, default: true },
    title: { type: String },
    theme: { type: String }
  },
  data () {
    return {
      // 页码
      pages: {
        type: Array,
        default () {
          return [1]
        }
      },
      // 总页数
      pageTotal: {
        type: Number,
        default: 1
      },
      activeNum: 0,
      first: -1,
      last: -1
    }
  },
  methods: {
    // 第一页
    wholePrevClick () {
      this.first = 1
      var newPages = []
      for (let i = 0; i < this.pages.length; i++) {
        newPages[i] = i + 1
      }
      this.pages = newPages
      this.activeNum = 0
      this.getData()
    },
    // 最后一页
    wholeNextClick () {
      this.last = this.pageTotal
      var newPages = []
      for (let i = 0; i < this.pages.length; i++) {
        newPages[this.pages.length - i - 1] = this.pageTotal - i
      }
      this.pages = newPages
      this.activeNum = this.pages.length - 1
      this.getData()
    },
    pageMake (index) {
      let newPages = []
      let len2 = parseInt(this.pages.length / 2)
      if (this.pages[index] <= len2 || this.pageTotal <= this.pageLen || this.pages[index] > this.pageTotal - len2) {
        for (let i = 0; i < this.pages.length; i++) {
          newPages[i] = this.pages[i]
        }
        this.activeNum = index
      } else if (this.pages[index] <= this.pageTotal - len2) {
        for (let i = 0; i < this.pages.length; i++) {
          newPages[i] = this.pages[index] - len2 + i
        }
        this.activeNum = len2
      }
      this.pages = newPages
      this.getData()
    },
    // 点击页码刷新数据
    onPageClick (index) {
      this.pageMake(index)
    },
    // 上一页
    onPrevClick () {
      // 当前页是否为当前最小页码
      if (this.activeNum > 0) {
        // this.activeNum = this.activeNum - 1;
        let index = this.activeNum - 1
        this.pageMake(index)
      } else {
        if (this.pages[0] !== 1) {
          let newPages = []
          for (let i = 0; i < this.pages.length; i++) {
            newPages[i] = this.pages[i] - 1
          }
          this.pages = newPages
          this.getData()
        }
      }
    },
    // 下一页
    onNextClick () {
      // 当前页是否为当前最大页码
      if (this.activeNum < this.pages.length - 1) {
        // this.activeNum = this.activeNum + 1
        let index = this.activeNum + 1
        this.pageMake(index)
      } else {
        if (this.pages[this.pages.length - 1] < this.pageTotal) {
          let newPages = []

          for (let i = 0; i < this.pages.length; i++) {
            newPages[i] = this.pages[i] + 1
          }
          this.pages = newPages
          this.getData()
        }
      }
    },
    // 获取页码
    getPages () {
      this.pages = []
      // 比较总页码和显示页数
      if (this.pageTotal <= this.pageLen) {
        // console.log(this.pageTotal+"....."+this.pageLen)
        for (let i = 1; i <= this.pageTotal; i++) {
          this.pages.push(i)
        }
      } else {
        for (let i = 1; i <= this.pageLen; i++) {
          this.pages.push(i)
        }
      }
    },
    // 页码变化获取数据
    getData () {
      var _self = this
      this.param[_self.currentPage] = this.pages[_self.activeNum]
      if (this.first !== -1) {
        this.param[_self.currentPage] = this.first
        this.first = -1
      } else if (this.last !== -1) {
        this.param[_self.currentPage] = this.last
        this.last = -1
      }
      this.$nextTick(function () {
        var _self = this
        var param = _self.param
        this.$http.get(_self.url, {params: param}).then(function (data) {
          // var data = data.body.data
          // _self.pageTotal = data.totalPage
          // data = data.data
          // if (_self.pages.length !== _self.pageLen || _self.pageTotal < _self.pageLen) {
          //   _self.getPages()
          // }
          // _self.$store.commit('changeRenderData', {data})
        })
      })
    },
    refresh () {
      this.pages = [1]

      this.activeNum = 0

      this.getData()
    }
  },
  created () {
    this.refresh()
    this.getData()
    this.getPages()
  }
}
</script>

<style scoped>
  .boot-select {
    float: left;
    width: 80px;
  }

  .boot-nav {
    float: right;
  }

  .boot-page {
    display: inline-block;
    margin: 2px 10px 0 20px;
    vertical-align: middle;
  }

  .page-total {
    display: inline-block;
    vertical-align: middle;
  }
</style>



