<template>
  <row>
    <div class="col-md-12">
    <!-- MAP & BOX PANE -->
      <div class="box box-success">
        <div class="box-header with-border">
          <h3 class="box-title">报表</h3>

          <div class="box-tools pull-right">
            <div class="block">
              <el-date-picker
                v-model="moth1"
                type="month"
                placeholder="选择开始月份">
              </el-date-picker>
              <span>至</span>
              <el-date-picker
                v-model="moth2"
                type="month"
                placeholder="选择结束月份">
              </el-date-picker>
              <button type="button" class="btn btn-primary" v-on:click="statistics">统计</button>
            </div>
          </div>
        </div>

        <div class="box-body">
          <va-table  slot="content">
            <tr>
              <td>月份</td>
              <td>上传文件</td>
              <td>数据表</td>
              <td>清洗完成</td>
              <td>转换完成</td>
              <td>检验完成</td>
              <td>完成比例</td>
              <td>处理数据条数</td>
            </tr>
            <tr >
              <td>3月</td>
              <td>35</td>
              <td>22</td>
              <td>55</td>
              <td>44</td>
              <td>83</td>
              <td>60%</td>
              <td>2333</td>
            </tr>
            <tr v-for="item in items">
              <td v-text="item.yf + '月'"></td>
              <td v-text="item.scjj"></td>
              <td v-text="item.sjb"></td>
              <td v-text="item.qxwc"></td>
              <td v-text="item.zgwc"></td>
              <td v-text="item.jywc"></td>
              <td v-text="item.wcbl + '%'"></td>
              <td v-text="item.clsjts"></td>
            </tr>
          </va-table>
        </div>
      </div>
    </div>
  </row>

</template>
 
<script>
import VABox from '../../components/VABox.vue'
import VATable from '../../components/VATable.vue'

export default {
  data () {
    return {
      items: [
        {
          'yf': 1,
          'scjj': 20,
          'sjb': 35,
          'qxwc': 66,
          'zgwc': 77,
          'jywc': 50,
          'wcbl': 80,
          'clsjts': 233
        }
      ],
      moth1: '',
      moth2: ''
    }
  },
  components: {
    'Va-box': VABox,
    'va-table': VATable
  },
  methods: {
    statistics: function () {
      if (this.moth1 === '') {
        this.$message({
          message: '请选择统计开始时间',
          type: 'warning'
        })
        return
      }
      if (this.moth2 === '') {
        this.$message({
          message: '请选择统计结束时间',
          type: 'warning'
        })
        return
      }
      console.log(this.moth1)
      console.log(this.moth2)
    }
  }
}
</script>
<style scoped>

</style>



