<template>
  <div class='box box-success'>
    <div class="box-header with-border">
      <h3 class="box-title">日志</h3>
    </div>
    <row>
      <div class="col-md-2">
        <div class="input-group" style="width:150px;margin:10px 0 0 20px;height:36px">
            <input type="text" class="form-control" placeholder="搜索">
            <span class="input-group-btn">
                <button class="btn btn-default" type="button"><i class="glyphicon glyphicon-search"></i></button>
            </span>
        </div>
      </div>
      <div class="col-md-2" style='margin:10px 0 0 20px'>
        <el-select v-model="mvalue" placeholder="按机器过滤">
          <el-option
            v-for="item in moptions"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="col-md-2" style='margin:10px 0 0 0'>
        <el-select v-model="tvalue" placeholder="按类型过滤">
          <el-option
            v-for="item in toptions"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="col-md-2 pull-right" style='margin:12px 0 0 0'>
        <button type='button' class='btn btn-primary'>导出</button>
      </div>
    </row>
    <div style="background: #fff;padding: 20px">
      <el-table
          :data="tableData"
          border
          style="width: 100%;margin-top: 40px;">
          <el-table-column
            prop="time"
            label="时间">
          </el-table-column>
          <el-table-column
            prop="machine"
            label="机器">
          </el-table-column>
          <el-table-column
            prop='type'
            label="类型">
          </el-table-column>
          <el-table-column
            prop="content"
            label="内容">
          </el-table-column>
        </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      //  机器下拉
      moptions:
      [
        { value: '所有服务器' },
        { value: '监控服务器' },
        { value: 'Web服务器' },
        { value: 'App服务器' },
        { value: '数据库服务器' }
      ],
      mvalue: '',
      //  下拉
      toptions:
      [
        { value: '所有类型' },
        { value: '记录' },
        { value: '警告' },
        { value: '错误' },
        { value: '严重错误' }
      ],
      tvalue: '',
      tableData: [{
        time: '2017-05-12 12:21:44',
        machine: '监控、Web、App、数据库',
        type: '记录',
        content: '服务器启动开始'
      },
      {
        time: '2017-05-12 12:21:44',
        machine: '监控、Web、App、数据库',
        type: '记录',
        content: '服务器启动开始'
      },
      {
        time: '2017-05-12 12:21:44',
        machine: '监控、Web、App、数据库',
        type: '记录',
        content: '服务器启动开始'
      },
      {
        time: '2017-05-12 12:21:44',
        machine: '监控、Web、App、数据库',
        type: '记录',
        content: '服务器启动开始'
      }
      ]
    }
  }
}
</script>




