import Vue from 'vue'
import Router from 'vue-router'

// data
import ClearData from 'pages/data/ClearData'
import Upload from 'pages/data/Upload'
import Sheet from 'pages/data/Sheet'
import Report from 'pages/data/Report'
import ListsClean from 'pages/data/ListsClean'
import ListsCompare from 'pages/data/ListsCompare'
import ListsFiles from 'pages/data/ListsFiles'
import ListsStore from 'pages/data/ListsStore'
import ListsTransform from 'pages/data/ListsTransform'
// monitor
import Logs from 'pages/monitor/Logs'
import Config from 'pages/monitor/Config'
import Dashboard from 'pages/monitor/Dashboard'
import Rules from 'pages/monitor/Rules'
import User from 'pages/monitor/User'
import Group from 'pages/monitor/Group'
import Authority from 'pages/monitor/Authority'
import Details from 'pages/monitor/Details'
// login
import Login from 'pages/Login'
// home
import Home from 'pages/Home'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      component: Home,
      children: [
        { path: '/clearData', component: ClearData },
        { path: '/upload', component: Upload },
        { path: '/sheet', component: Sheet },
        { path: '/report', component: Report },
        { path: '/listsClean', component: ListsClean },
        { path: '/listsCompare', component: ListsCompare },
        { path: '/listsFiles', component: ListsFiles },
        { path: '/listsStore', component: ListsStore },
        { path: '/listsTransform', component: ListsTransform },
        { path: '/logs', component: Logs },
        { path: '/config', component: Config },
        { path: '/dashboard', component: Dashboard },
        { path: '/rules', component: Rules },
        { path: '/user', component: User },
        { path: '/group', component: Group },
        { path: '/authority', component: Authority },
        { path: '/details', component: Details }
      ]
    }
  ],
  linkActiveClass: 'active'
})



